'use client';

import { motion } from "framer-motion";
import { Brain, Hand, Heart } from "lucide-react";
import { useEffect, useState } from "react";

interface ObjectivesProps {
  keralaMapUrl?: string;
}

export default function Objectives({ keralaMapUrl = "/images/kerala-map.png" }: ObjectivesProps) {
  const [typedCode, setTypedCode] = useState("");
  const codeSnippet = `const FutureMaker = () => {
  return (
    <Innovation>
      <Passion />
      <Skills />
      <Impact />
    </Innovation>
  );
};`;

  useEffect(() => {
    let index = 0;
    const interval = setInterval(() => {
      if (index < codeSnippet.length) {
        setTypedCode(codeSnippet.slice(0, index + 1));
        index++;
      } else {
        clearInterval(interval);
      }
    }, 50);

    return () => clearInterval(interval);
  }, []);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.6,
      },
    },
  };

  return (
    <>
      <h1 className="text-center text-4xl md:text-6xl lg:text-7xl font-bold mb-4 pb-3 bg-linear-to-r from-purple-400 to-purple-600 bg-clip-text text-transparent">Our Objectives</h1>
      <div className="min-h-screen bg-background flex items-center justify-center p-4 md:p-8">
        <motion.div
          variants={containerVariants}
          initial="hidden"
          animate="visible"
          className="w-full max-w-7xl grid grid-cols-1 md:grid-cols-3 gap-4 md:gap-6"
        >
          {/* Main Card - Kerala Map with Glowing Nodes */}
          <motion.div
            variants={itemVariants}
            className="md:col-span-2 md:row-span-2 glass-card glow-hover rounded-3xl p-8 md:p-12 relative overflow-hidden group border border-white/10 hover:border-purple-500/50 transition-colors duration-300"
          >
            <div className="absolute inset-0 opacity-30">
              <img
                src={keralaMapUrl}
                alt="Kerala Map"
                className="w-full h-full object-cover"
              />
            </div>
            <div className="absolute inset-0 bg-linear-to-br from-purple-500/10 via-transparent to-transparent" />

            {/* Glowing connection nodes */}


            <div className="relative z-10">
              <h1 className="text-4xl md:text-6xl lg:text-7xl font-bold mb-4 bg-linear-to-r from-purple-400 to-purple-600 bg-clip-text text-transparent">
                Future Makers
              </h1>
              <p className="text-xl md:text-2xl text-muted-foreground max-w-2xl">
                Empowering Kerala's next generation of innovators, creators, and changemakers
              </p>
            </div>
          </motion.div>

          {/* Side Card - Code Typing Animation with Glassmorphism */}
          <motion.div
            variants={itemVariants}
            className="md:row-span-2 glass-card glow-hover rounded-3xl p-6 md:p-8 relative overflow-hidden border border-white/10 hover:border-purple-500/50 transition-colors duration-300"
          >
            <div className="absolute inset-0 bg-linear-to-br from-purple-500/5 via-transparent to-transparent" />
            <div className="relative z-10 h-full flex flex-col">
              <div className="flex items-center gap-2 mb-4">
                <div className="w-3 h-3 rounded-full bg-red-500" />
                <div className="w-3 h-3 rounded-full bg-yellow-500" />
                <div className="w-3 h-3 rounded-full bg-green-500" />
              </div>
              <pre className="text-xs md:text-sm text-purple-400 font-mono flex-1 overflow-auto">
                <code>{typedCode}</code>
                <span className="inline-block w-2 h-4 bg-purple-400 animate-pulse ml-1" />
              </pre>
            </div>
          </motion.div>

          {/* Bottom Card 1 - Stat Card */}
          <motion.div
            variants={itemVariants}
            className="glass-card glow-hover rounded-3xl p-6 md:p-8 relative overflow-hidden group border border-white/10 hover:border-purple-500/50 transition-colors duration-300"
          >
            <div className="absolute inset-0 bg-linear-to-br from-purple-500/10 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500" />
            <div className="relative z-10">
              <div className="text-5xl md:text-6xl font-bold bg-linear-to-r from-purple-400 to-purple-600 bg-clip-text text-transparent drop-shadow-[0_0_20px_rgba(147,51,234,0.6)] mb-2">
                100,000+
              </div>
              <p className="text-lg md:text-xl text-muted-foreground">Learners Across Kerala</p>
            </div>
          </motion.div>

          {/* Bottom Card 2 - Philosophy Card */}
          <motion.div
            variants={itemVariants}
            className="md:col-span-2 glass-card glow-hover rounded-3xl p-6 md:p-8 relative overflow-hidden border border-white/10 hover:border-purple-500/50 transition-colors duration-300"
          >
            <div className="absolute inset-0 bg-linear-to-r from-purple-500/5 via-transparent to-purple-500/5" />
            <div className="relative z-10 flex justify-around items-center h-full">
              <motion.div
                animate={{ y: [0, -20, 0] }}
                transition={{
                  duration: 3,
                  repeat: Infinity,
                  ease: "easeInOut",
                }}
                className="flex flex-col items-center gap-3"
              >
                <div className="w-16 h-16 md:w-20 md:h-20 rounded-2xl glass-card flex items-center justify-center shadow-[0_0_30px_rgba(147,51,234,0.4)]">
                  <Brain className="w-8 h-8 md:w-10 md:h-10 text-purple-400" />
                </div>
                <span className="text-sm md:text-base text-muted-foreground font-medium">Understand</span>
              </motion.div>

              <motion.div
                animate={{ y: [0, -20, 0] }}
                transition={{
                  duration: 3,
                  repeat: Infinity,
                  ease: "easeInOut",
                  delay: 0.3,
                }}
                className="flex flex-col items-center gap-3"
              >
                <div className="w-16 h-16 md:w-20 md:h-20 rounded-2xl glass-card flex items-center justify-center shadow-[0_0_30px_rgba(147,51,234,0.4)]">
                  <Hand className="w-8 h-8 md:w-10 md:h-10 text-purple-400" />
                </div>
                <span className="text-sm md:text-base text-muted-foreground font-medium">Build</span>
              </motion.div>

              <motion.div
                animate={{ y: [0, -20, 0] }}
                transition={{
                  duration: 3,
                  repeat: Infinity,
                  ease: "easeInOut",
                  delay: 0.6,
                }}
                className="flex flex-col items-center gap-3"
              >
                <div className="w-16 h-16 md:w-20 md:h-20 rounded-2xl glass-card flex items-center justify-center shadow-[0_0_30px_rgba(147,51,234,0.4)]">
                  <Heart className="w-8 h-8 md:w-10 md:h-10 text-purple-400" />
                </div>
                <span className="text-sm md:text-base text-muted-foreground font-medium">Create</span>
              </motion.div>
            </div>
          </motion.div>
        </motion.div>
      </div>
    </>
  );
}
